body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
  font-family: "apercu-regular", sans-serif;
}
a {
  color: #000;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  padding-bottom: 80px;
  min-height: 100vh;
}
header {
  display: block;
  width: 100%;
  text-align: left;
  position: relative;
  margin-bottom: 40px;
}
header .logo {
    z-index: 9;
    width: 40px;
    height: 30px;
    margin: 30px 0 0 30px;
    position: relative;
}
@media (min-width: 1200px) {
header .logo {
        margin: 40px 0 0 40px;
}
}
header .menu-button {
    position: absolute;
    z-index: 999;
    top: 0;
    right: 0;
    margin: 30px 25px 0 0;
    outline: none;
    background: transparent;
    border: none;
}
@media (min-width: 800px) {
header .menu-button {
        display: none;
}
}
header .menu-button span {
      display: block;
      width: 23px;
      height: 2px;
      margin-bottom: 5px;
      position: relative;
      background: #000;
      -webkit-transform-origin: 4px 0px;
              transform-origin: 4px 0px;
      -webkit-transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
      transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
      transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
      transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
header .menu-button span:first-of-type {
        -webkit-transform-origin: 0% 0%;
                transform-origin: 0% 0%;
}
header .menu-button span:nth-of-type(3) {
        -webkit-transform-origin: 0% 100%;
                transform-origin: 0% 100%;
}
header .menu-button.active span:first-of-type {
      opacity: 1;
      -webkit-transform: rotate(45deg) translate(-2px, -1px);
              transform: rotate(45deg) translate(-2px, -1px);
}
header .menu-button.active span:nth-of-type(2) {
      opacity: 0;
      -webkit-transform: rotate(0deg) scale(0.2, 0.2);
              transform: rotate(0deg) scale(0.2, 0.2);
}
header .menu-button.active span:nth-of-type(3) {
      -webkit-transform: rotate(-45deg) translate(0, -1px);
              transform: rotate(-45deg) translate(0, -1px);
}
header ul {
    list-style: none;
    margin-top: 140px;
}
@media (min-width: 800px) {
header ul {
        display: inline;
}
}
header ul li {
      margin: 30px 0;
}
@media (min-width: 800px) {
header ul li {
          display: inline-block;
          margin: 0;
}
}
header ul li a {
        text-decoration: none;
        font-weight: bold;
        margin: 0;
        font-size: 20px;
}
@media (min-width: 800px) {
header ul li a {
            margin: 0 10px;
            font-size: 12px;
}
header ul li a.router-link-active {
              border-bottom: 2px solid #000;
}
}
header nav {
    text-transform: uppercase;
    position: fixed;
    z-index: 9;
    background-color: #8DFFBD;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
}
header nav:not(.active) {
      right: 0;
      left: 100%;
      bottom: 0;
      top: 0;
}
@media (min-width: 800px) {
header nav {
        display: block;
}
header nav, header nav:not(.active) {
          position: absolute;
          left: 60px;
          top: 40px;
          bottom: auto;
          right: auto;
          background: transparent;
}
}
@media (min-width: 800px) and (min-width: 1200px) {
header nav, header nav:not(.active) {
        top: 50px;
}
}
header #contact-info {
    position: absolute;
    right: 35px;
    top: 40px;
    display: none;
}
@media (min-width: 800px) {
header #contact-info {
        display: block;
}
}
@media (min-width: 1200px) {
header #contact-info {
        top: 50px;
        right: 40px;
}
}
section {
  margin: 0;
  overflow-x: hidden;
}
@media (min-width: 800px) {
section {
      margin: 0 60px;
}
}
@media (min-width: 800px) {
.sm-only {
    display: none;
}
}
.sm-hidden {
  display: none;
}
@media (min-width: 800px) {
.sm-hidden {
      display: inherit;
}
}
a.cta-button {
  text-decoration: none;
  font-size: 12px;
  font-weight: 300;
  border: 2px solid #000;
  padding: 15px 30px;
  display: inline-block;
}
.bg-wrapper {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  min-height: 20px;
  position: absolute;
  right: 0;
  top: 0;
  width: 1000px;
  z-index: -1;
  overflow: hidden;
}
@media (min-width: 800px) {
.bg-wrapper {
      right: 100px;
      overflow-x: visible;
}
}
#footer {
  z-index: 999;
  opacity: 0.4;
  font-size: 12px;
}
#copyright {
  position: absolute;
  bottom: 10px;
  text-align: center;
  left: 0;
  right: 0;
}



