@media (min-width: 1200px) {
#work-with-us {
    width: 1000px;
    margin: 0 auto;
}
}
#work-with-us .copy {
  text-align: center;
  width: 80%;
  margin: 0 auto;
}
#work-with-us .copy img {
    width: 80%;
    margin: 20px auto 0 -100px;
}
@media (min-width: 800px) {
#work-with-us .copy img {
        margin: 60px auto 0 -50px;
}
}
@media (min-width: 1200px) {
#work-with-us .copy img {
        margin: 100px auto 0 -100px;
}
}
#work-with-us .copy p:first-of-type {
    margin-top: 80px;
}
#work-with-us .copy #letter {
    font-family: "NewCenturySchlbkLTPro-Bd", "schoolbook", Georgia, Times, "Times New Roman", serif;
    font-size: 20px;
}
#work-with-us .copy form {
    margin: 80px auto 120px;
    max-width: 450px;
    text-align: left;
}
#work-with-us .copy form input, #work-with-us .copy form select, #work-with-us .copy form textarea {
      font-family: "apercu-regular", sans-serif;
      font-weight: bold;
      padding: 10px;
      font-size: 16px;
      display: block;
      outline: none;
      text-align: left;
      color: #000;
      width: 100%;
      border-radius: 0;
      font-size: 14px;
      padding: 14px 20px 10px;
      background: #ededed;
      border: none;
      text-transform: lowercase;
      width: calc(100% - 40px);
      margin: 20px 0;
      resize: none;
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
}
#work-with-us .copy form select {
      width: 100%;
      font-weight: bold;
}
#work-with-us .copy form select option {
        font-weight: bold;
}
#work-with-us .copy form select:invalid,
      #work-with-us .copy form select option[value=""] {
        font-weight: normal;
}
#work-with-us .copy form button {
      width: 30%;
      text-align: center;
      margin-top: 10px;
      border: 2px solid #000;
      background: #fff;
      text-transform: capitalize;
      font-size: 12px;
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      border-radius: 0;
      padding: 11px;
      margin: 10px 0 0;
      color: #000;
}
@media (min-width: 800px) {
#work-with-us .copy form button {
          margin: 0;
}
}
#work-with-us .copy form button[disabled="disabled"] {
        opacity: 0.5;
}
#work-with-us .copy form ::-webkit-input-placeholder {
      /* WebKit, Blink, Edge */
      color: #000;
      font-weight: normal;
}
#work-with-us .copy form :-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #000;
      opacity: 1;
      font-weight: normal;
}
#work-with-us .copy form ::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #000;
      opacity: 1;
      font-weight: normal;
}
#work-with-us .copy form :-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #000;
      font-weight: normal;
}
#work-with-us .copy form ::-ms-input-placeholder {
      /* Microsoft Edge */
      color: #000;
      font-weight: normal;
}
#work-with-us p {
  margin-top: 40px;
  text-align: left;
  font-size: 16px;
  line-height: 1.4em;
}
@media (min-width: 800px) {
#work-with-us p {
      font-size: 14px;
      width: 450px;
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
}
}
#work-with-us p a {
    border-bottom: 1px #000 solid;
}
#work-with-us .error p {
  padding: 20px;
  background: rgba(255, 0, 0, 0.4);
}
#work-with-us .overlay {
  position: fixed;
  top: 100px;
  bottom: 0;
  left: 0;
  right: 0;
}
#work-with-us .overlay .img img {
    width: 100%;
}



