@media (min-width: 1200px) {
#sign-up {
    width: 1000px;
    margin: 0 auto;
}
}
#sign-up .copy {
  text-align: center;
  width: 80%;
  margin: 0 auto;
}
#sign-up .copy img {
    width: 80%;
    margin: 20px auto 0 -100px;
}
@media (min-width: 800px) {
#sign-up .copy img {
        width: 40%;
        margin: 60px auto 0 -50px;
}
}
@media (min-width: 1200px) {
#sign-up .copy img {
        margin: 100px auto 0 -100px;
}
}
#sign-up .copy form {
    margin: 60px auto 0;
    max-width: 450px;
}
@media (min-width: 800px) {
#sign-up .copy form #mc_embed_signup_scroll .mc-field-group, #sign-up .copy form #mc_embed_signup_scroll .submit {
        float: left;
}
#sign-up .copy form #mc_embed_signup_scroll .mc-field-group {
        width: 60%;
        margin-right: 5%;
}
#sign-up .copy form #mc_embed_signup_scroll .submit {
        width: 35%;
}
}
#sign-up .copy form #mc_embed_signup_scroll:after {
      content: "";
      display: block;
      clear: both;
}
#sign-up .copy form label {
      display: none;
}
#sign-up .copy form input {
      padding: 10px;
      font-size: 16px;
      display: block;
      outline: none;
      text-align: left;
      color: #000;
      width: 100%;
      border-radius: 0;
}
@media (min-width: 800px) {
#sign-up .copy form input {
          width: 100%;
          margin: 0;
}
}
#sign-up .copy form input[type="email"] {
        font-size: 14px;
        padding: 14px 20px 10px;
        background: #ededed;
        border: none;
        text-transform: lowercase;
        width: calc(100% - 40px);
}
#sign-up .copy form input[type="submit"] {
        text-align: center;
        margin-top: 10px;
        border: 2px solid #000;
        background: #fff;
        text-transform: capitalize;
        font-size: 12px;
        -webkit-appearance: none;
}
@media (min-width: 800px) {
#sign-up .copy form input[type="submit"] {
            margin: 0;
            padding: 11px;
}
}
#sign-up .copy form input[type="submit"][disabled="disabled"] {
          opacity: 0.1;
}
#sign-up .copy form ::-webkit-input-placeholder {
      /* WebKit, Blink, Edge */
      color: #000;
}
#sign-up .copy form :-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #000;
      opacity: 1;
}
#sign-up .copy form ::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #000;
      opacity: 1;
}
#sign-up .copy form :-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #000;
}
#sign-up .copy form ::-ms-input-placeholder {
      /* Microsoft Edge */
      color: #000;
}
#sign-up p {
  margin-top: 40px;
  text-align: left;
  font-size: 16px;
  line-height: 1.4em;
}
@media (min-width: 800px) {
#sign-up p {
      font-size: 14px;
      width: 450px;
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
}
}
#sign-up #previous-newsletters {
  margin-top: 100px;
}
@media (min-width: 800px) {
#sign-up #previous-newsletters {
      margin: 100px auto;
      width: 450px;
      max-width: 450px;
      text-align: center;
}
}
#sign-up #previous-newsletters h2 {
    margin-bottom: 50px;
    text-align: left;
}
#sign-up #previous-newsletters ul {
    margin: 0 auto;
    text-align: left;
    padding: 0;
}
#sign-up #previous-newsletters ul li {
      list-style: none;
      position: relative;
      margin: 30px 0 10px;
      line-height: 1.2;
}
#sign-up #previous-newsletters ul li a {
        text-decoration: none;
        border-bottom: 1px solid #000;
}
#sign-up #previous-newsletters ul li .date {
        font-size: .6em;
        color: blue;
        font-weight: bold;
        display: block;
        margin-top: 5px;
}
@media (min-width: 800px) {
#sign-up #previous-newsletters ul li .date {
            display: inline;
}
}



